ul, ol {
    padding: 0 2rem;
}

textarea {
    height: 10em;
    resize: vertical;
}

.cond2-wrapper {
    height: 100%;
    width: 100%;
    /* max-height: 790px; */
    /* max-width: 1280px; */
    max-height: 740px;
    max-width: 1080px;
}

.card {
    padding: 1rem 2rem;
    border: none !important;
}

.hint-text {
    color: var(--good-blue);
    font-weight: bold;
    font-style: italic;
}

main {
    background-color: white;
    height: calc(100% - 52px - 54px - 46px);
    min-height: calc(100% - 52px - 54px - 46px);
    width: 100%;
    margin: auto !important;
}

.main-outline {
    height: 100%;
    max-height: 100vh;
    display: flex;
    flex-direction: column;
}

.header-bar {
    border: 1px solid #dddd;
    border-style: none none solid none;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
}

.header-bar>.title {
    color: #3f3fbb;
}


/* .main-nav {
    margin: 0 auto;
    display: flex;
    gap: 2rem;
    padding-bottom: 6px;
    padding-left: 1rem;
    padding-right: 1rem;
} */

.phaseTabs {
    background-color: var(--good-blue);
    font-weight: bold;
    font-size: 2em;
    /* width: 2em; */
    /* height: 2em; */
    /* display: flex; */
    /* align-items: center;
    justify-content: center; */
    border-radius: 0;
}

.phaseTabs.selected-btn {
    outline: 4px solid #333;
    /* border-radius: 0.5em; */
}

.controller-nav {
    margin-top: auto;
    width: 100%;
    display: flex;
    padding: 0.25rem;
}

.controller-nav .prev {
    margin-right: auto;
}

.controller-nav .next {
    margin-left: auto;
}

/* .controller-nav .nav-btn {
    background-color: var(--good-blue);
    font-weight: bold;
    font-style: italic;
    font-size: 1.2em;
} */

.main-view {
    display: flex;
    flex-direction: column;
    flex-grow: 0;
    align-items: center;
    height: 100%;
    overflow: scroll;
}

.main-view .card {
    display: flex;
    flex-direction: column;
    font-size: 1.5rem;
    border: none;
    flex-grow: 0;
    max-height: 100%;
    overflow: auto;
}

.card .card-body {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    flex-grow: 0;
}

.two-div-outline-container {
    display: flex;
    column-gap: 4px !important;
    width: 100%;
    /* height: 100%; */
    padding: 0.25rem;
    /* font-size: 1.3rem; */
    font-size: 1rem;
    gap: 1rem;
    /* height: 100%; */
    overflow: hidden;
}


.two-div-outline-container>.outline {
    border-right: 1px solid #333;
    padding-left: 0.25rem;
    padding-right: 0.25rem;
    flex: 0 0 30%;
    overflow: auto;
}

.btn-link.disabled,
.btn-link:disabled {
    background-color: white !important;
    color: gray !important;
}

.two-div-outline-container>.outline ul {
    list-style-type: none;
}

.two-div-outline-container>.outline li {
    /* text-decoration: underline; */
    margin-bottom: 1rem;
    /* cursor: pointer; */
}

.two-div-outline-container>.outline li.disabled {
    text-decoration: none;
    color: #bbb;
    user-select: none;
    pointer-events: none;
    cursor: default;
}

.two-div-outline-container>.outline li.active::before {
    content: "~";
    display: inline-block;
    margin-right: 0.5rem;
}

.two-div-outline-container>.outline-rhs {
    /* padding: 1rem; */
    padding-left: 4px;
    padding-right: 4px;
    height: 100%;
    overflow-y: hidden;
}

.feedback-block {
    height: 100%;
    font-size: 0.9rem;
    overflow-y: hidden;
}

.feedback-block table {
    margin-bottom: 0.5rem;
}

.feedback-block .table-sm th,
.feedback-block .table-sm td {
    padding: 2.4px;
}

.feedback-list {
    height: 100%;
    overflow-y: hidden;
}

.slideshow a {
    color: var(--good-blue) !important;
    font-style: italic;
    cursor: pointer;
}

.benchmark textarea {
    width: 100%;
    height: 5rem;
}

.benchmark .save-btn {
    margin-left: auto;
}

.benchmark .save-btn:hover {
    background-color: #7b7bc5;
}